<template>
  <n-data-table :columns="columns" :data="data"  />

</template>

<script>
import {getRequest} from "../../api/request";
import {h, onMounted, ref} from "vue";
import {useRouter} from 'vue-router'
import {NAvatar, NButton, NDatePicker, NText, NTime} from "naive-ui";

const createColumns = ({ sendMail, toExamine }) => {
      return [
        {
          title: '预约行程id',
          key: 'actions',
          render(row){
            return h(NText,{

            },{
              default:()=>row.id
            })
          }
        },
        {
          title: 'Name',
          key: 'actions',
          render(row){
            return h(NText,{

            },{
              default:()=>row.user.name
            })
          }
        },
        {
          title: '头像',
          key: 'actions',
          render(row){
            return h(NAvatar,{
              src:row.user.userface

            },{

            })
          }
        },
        {
          title: '预约地址',
          key: 'actions',
          render(row){
            return h(NText,{

            },{
              default:()=>row.rentaldemand.location+row.rentaldemand.community+row.rentaldemand.building+"栋"+row.rentaldemand.door
            })
          }
        },
        {
          title: '预约电话',
          key: 'actions',
          render(row){
            return h(NText,{

            },{
              default:()=>row.rentaldemand.demandPhone
            })
          }
        },
        {
          title: '预约时间',
          key: 'actions',
          render(row){
            return h(NDatePicker,{
              value:row.appointmentDate,

              type:'datetime'

            },{

            })
          }
        },
        {
          title: '操作',
          key: 'actions',
          render(row){
            return h(NButton,{
              type:'info',
              size:'tiny',
              onClick:()=>
              toExamine(row)


            },{
              default:()=>'开始审核'
            })
          }
        }
      ]}

export default {
  name: "Mine",


  setup(){
    const data=ref([])
    // const sendData=ref([])
    const router=useRouter()
    function getMine() {
      getRequest('/appointment/getMine').then(resp=>{
        data.value=resp.data


      })
    }

    onMounted(getMine)
    return{
      data,
      getMine,

      columns: createColumns({
toExamine(row){
  router.push({path:'/newHouse',name:'newHouse',query:
        {
          data:row.id

        }
  })
}
      })
      // sendData


    }
  }
}
</script>

